import React, { memo } from 'react'
import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider'
import { StylesProvider, createGenerateClassName, jssPreset } from '@material-ui/styles'
import { create } from 'jss'
import { theme, Header } from 'unicef-material-ui'
import './App.css'
import { NavLinks, NavTabs, Form, CardWithTabs, Cards, Alert } from './components'
import { makeStyles } from '@material-ui/core/styles'
import {
  ListItemText,
  Badge,
  List,
  ListItem,
  Collapse
} from '@material-ui/core';
import MailIcon from '@material-ui/icons/Mail';
import NotificationsIcon from '@material-ui/icons/Notifications';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import StarBorder from '@material-ui/icons/StarBorder';

// import avatar from './assets/avatar.png'
const useStyles = makeStyles(theme => ({
  list: {
    width: 250,
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
  boxShadow: {
    boxShadow: 'none'
  },
  menuItemPadding : {
    padding: '4px 8px !important'
  }
}));

const generateClassName = createGenerateClassName({
  productionPrefix: 'c',
});

const jss = create({
  ...jssPreset(),
  // Define a custom insertion point that JSS will look for when injecting the styles into the DOM.
  insertionPoint: 'jss-insertion-point',
});

function App() {

  const classes = useStyles();
  const [openDropdown, setOpenDropdown] = React.useState(false)

  const handleDropdown = () => {
    setOpenDropdown(!openDropdown)
  }
  const tabs = [{ name: 'Active', type: 'normal', link: "/header" }, { name: 'Disabled', type: 'disabled', link: null }, { name: 'Directory', type: 'normal', link: null }]

  const MenuItems = <React.Fragment>
    <ListItem button key="With Icon" className={classes.menuItemPadding}>
      <ListItemIcon> <MailIcon /> </ListItemIcon>
      <ListItemText primary="With Icon" />
    </ListItem>
    <ListItem button key="Notifications" className={classes.menuItemPadding}>
      <ListItemIcon> <Badge badgeContent={17} color="secondary">
        <NotificationsIcon />
      </Badge> </ListItemIcon>
      <ListItemText primary="Notifications" />
    </ListItem>
    <ListItem button key="Dropdown" onClick={handleDropdown} className={classes.menuItemPadding}>
      <ListItemIcon>{openDropdown ? <ExpandLess /> : <ExpandMore />}</ListItemIcon>
      <ListItemText primary="Dropdown" />
    </ListItem>
    <Collapse in={openDropdown} timeout="auto" unmountOnExit>
      <List component="div" disablePadding>
        <ListItem button className={`${classes.nested} ${classes.menuItemPadding}`}>
          <ListItemIcon>
            <StarBorder />
          </ListItemIcon>
          <ListItemText primary="Starred" />
        </ListItem>
      </List>
    </Collapse>
  </React.Fragment>

  // Example tab
  const tab = <React.Fragment>
    <ListItem button key="Active" className={classes.menuItemPadding} selected>
      <ListItemText primary="Active" />
    </ListItem>
    <ListItem button key="Disbaled" disabled className={classes.menuItemPadding}>
      <ListItemText primary="Disbaled" />
    </ListItem>
    <ListItem button key="Directory" className={classes.menuItemPadding}>
      <ListItemText primary="Directory" />
    </ListItem>
  </React.Fragment>



  return (
    <MuiThemeProvider theme={theme}>
      <StylesProvider generateClassName={generateClassName}>
        <StylesProvider jss={jss}>
          <Header
            applicationName="Application"
            navLinks={<NavLinks />}
            tabs={<NavTabs tabs={tabs} />}
            // hideLogo={false}
            // logoBorderLine={false}
            // logo={<img alt="user" src={avatar} />}
            menuItems={MenuItems}
            menuTabs={tab}
            menuButton={true}
          />
          <div className="margin-top">
            <Form />
            <Alert />
            <CardWithTabs />
            <Cards />
          </div>
        </StylesProvider>
      </StylesProvider>
    </MuiThemeProvider>
  )
}
export default memo(App)
